.crt::after {
  content: " ";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
}
.crt.flicker::before{
	-webkit-animation: flicker 0.15s infinite;
	        animation: flicker 0.15s infinite;
}
.crt::before {
  content: " ";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(18, 16, 16, 0)), color-stop(50%, rgba(0, 0, 0, 0.25))), -webkit-gradient(linear, left top, right top, from(rgba(255, 0, 0, 0.06)), color-stop(rgba(0, 255, 0, 0.02)), to(rgba(0, 0, 255, 0.06)));
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}
.crt {
  -webkit-animation: textShadow 1.6s infinite;
          animation: textShadow 1.6s infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.crt.flicker {
	-webkit-animation-play-state: running;
	        animation-play-state: running;
}

main {
	flex: 1 1 auto;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	width: 100%;
	margin: 0;
	padding: 0;
}

main article {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	justify-content: flex-end;
	width: 100%;
	max-width: 40ch;
	background: #0006;
	border: 1px solid #fff6;
	border-bottom: none;
	margin: 1rem;
}

main article figure {
	/* flex: 3 1 350px; */
	max-height: 40ch;
	height: auto;
	width: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
	/* background: #333; */
	border-bottom: 1px solid #fff6;
	background: radial-gradient(#666, #333);
}

main article figure img {
	width: 100%;
	height: auto;
	object-fit: cover;
	object-position: top center;
	padding: 0;
	margin: 0;
	-webkit-filter: grayscale(90%);
	        filter: grayscale(90%);
	-webkit-transition: -webkit-filter 0.5s ease-in-out;
	transition: -webkit-filter 0.5s ease-in-out;
	transition: filter 0.5s ease-in-out;
	transition: filter 0.5s ease-in-out, -webkit-filter 0.5s ease-in-out;
}

main article figure img:hover {
	-webkit-filter: grayscale(0%);
	        filter: grayscale(0%);
}

main article section {
	flex: 0 1 auto;
	display: flex;
	margin: 0;
	padding: 0;
	border-bottom: 1px solid#fff6;
	align-items: stretch;
	justify-content: center;
}

main article section h3 {
	flex: 1 1;
	margin: 0;
	border-right: 1px solid #fff6;
	display: flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	font-weight: normal;
}

main article section p {
	flex: 3 3;
	text-align: left;
	margin: 0;
	padding-left: 1rem;
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.App {
	text-align: center;
	font-family: 'IBM Plex Mono', monospace;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	color: white;
	padding: 0;
	margin: 0;
}

html::before {
	content: 'TEA-OSv1.5 \A ©the8bitMoon';
	white-space: pre;
	position: fixed;
	height: 100vh;
	width: 100vw;
	font-size: 0.5rem;
	color: white;
	background: url(/WFF-Profiles/static/media/Future_Foundation_symbol.29c87d4b.png), -webkit-gradient(linear, left top, left bottom, from(#333), color-stop(#096), to(#333));
	background: url(/WFF-Profiles/static/media/Future_Foundation_symbol.29c87d4b.png), linear-gradient(#333, #096, #333);
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	z-index: -100;
	font-family: 'IBM Plex Mono', monospace;
	padding: 0.1rem;
	-webkit-filter: brightness(50%);
	        filter: brightness(50%);
}

footer {
	padding-bottom: 1rem;
}

footer small button {
	color: white;
	background: #0003;
	border: 1px solid #fff3;
	padding: 0.5em;
	z-index: -50;
}

